
.main-container{

  display:grid;
  display:flex;
  flex-direction: rows;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: url('./drum_machine_bck.jpg') center center/cover no-repeat;


}




.buttons{
  width: 100%;
  text-decoration: none;
  /* invisible border (will be colored on hover/focus) */
  border: solid 1px transparent;
  
  /* size comes from text & padding (no width/height) */
  padding: 0.5em 1em;
  /* make sure colors have enough contrast! */
  color:yellow;
  background-color: #534758;
}

.buttons-container{
  width: 80%;
  max-width: 700px;
  display: grid;
  padding: 1rem;
  grid-template-rows: 2fr 1fr 1fr;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 0.5rem;
 
  padding:10px;
}

.pressed-btn {
  box-shadow:4px 2px 30px -4px rgba(255,255,0,9);
  transition: all 0.3s
}





.app-title{
    text-align: center;
    vertical-align: center;
    grid-column-start: 1;
    grid-column-end: span 3;
    color:yellow;
    font-weight: bold;
    font-size: 1.3rem;

}


@media (max-width: 500px){
  .buttons-container{
    grid-template-rows: 2fr 1fr;
    grid-template-columns: 1fr 1fr;
    grid-gap: 0.1rem;

  }

  .app-title{
    grid-column-end: span 2;
    align-self: center;
  }


}


@media (max-width: 300px){
  .buttons-container{
    grid-template-rows: 1fr;
    grid-template-columns: 1fr;
    grid-gap: 0.1rem;

  }

  .app-title{
    grid-column-end: span 1;
  }


}